import * as React from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChevronRight } from '@fortawesome/free-solid-svg-icons';
import { TimelineEvent, Timeline } from '../ext';
import { Row, Col, Grid } from "react-bootstrap"
import { Article } from '../Article';
import { Pages } from '../../App';
import { Definition } from './Definitions';

const timeline = [{
    name: "IEEE 802.11",
    year: "1997",
    text: "Freq: 2,4 GHz, max Datenrate: 2 MBit/s"
},{
    name: "IEEE 802.11b",
    year: "1999",
    text: "Freq: 2,4 GHz, max Datenrate: 11 MBit/s"
},{
    name: "IEEE 802.11g",
    year: "2003",
    text: "Freq: 2,4 GHz, max Datenrate: 54 MBit/s"
},{
    name: "IEEE 802.11n",
    year: "2009",
    text: "Freq: 2,4 GHz, max Datenrate: 600 MBit/s"
},{
    name: "IEEE 802.11ad WiGig",
    year: "2012",
    text: "Freq: 60 GHz, max Datenrate: 4,8 GBit/s"
},{
    name: "IEEE 802.11ac Wave 1",
    year: "2013",
    text: "???"
}, {
    name: "IEEE 802.11ac Wave 2",
    year: "2015",
    text: "Freq: 2,4 GHz, max Datenrate: 2 MBit/s"
}, {
    name: "IEEE 802.11ah sub-1 GHz",
    year: "2016",
    text: "Freq: 2,4 GHz, max Datenrate: 2 MBit/s"
},{
    name: <Definition defi="IEEE 802.11ay">IEEE 802.11ay</Definition>,
    year: "2018",
    text: "Freq: 60 GHz, max Datenrate: 176 GBit/s"
},];
/*
1997 : IEEE 802.11
Freq: 2,4 GHz
max Datenrate: 2 MBit/s

1999 : IEEE 802.11b
Freq: 2,4 GHz
max Datenrate: 11 MBit/s

2003 : IEEE 802.11g
Freq: 2,4 GHz
max Datenrate: 54 MBit/s

2009 : IEEE 802.11n
Freq: 2,4 / 5 GHz
max Datenrate: 600 MBit/s

2012 : IEEE 802.11ad WiGig
Freq: 60 GHz
max Datenrate: 4,6 GBit/s

2013 : IEEE 802.11ac Wave 1
2015 : IEEE 802.11ac Wave 2
Freq: 2,4 GHz
max Datenrate: 2 MBit/s

2016 : IEEE 802.11 ah sub-1 GHz
Freq: 5 GHz
max Datenrate: 54 MBit/s

2018 : IEEE 802.11ay
Freq: 60 GHz
max Datenrate: 176 GBit/s

*/

export class Merkmale extends React.Component{

    private timelineGenerator() {
        return timeline.map(e => <TimelineEvent
            iconColor={e.year === "2018" ? "green" : "steelblue"}
            title={<span style={{fontWeight: e.year === "2018" ? "bold": undefined}}>
                    {e.name}
                </span>}
            createdAt={e.year}
            icon={<FontAwesomeIcon icon={faChevronRight}/>}
        >
            <span style={{fontWeight: e.year === "2018" ? "bold": undefined}}>
                {e.text}
            </span>
        </TimelineEvent>).reverse()
    }

    public render() {
        return(
            <Grid>
                <Row>
                    <Col xs={12}>
                        <div className="customCard">
                            <h3>Merkmale von WLAN</h3>
                        </div>
                    </Col>
                </Row>
                <Row>
                    <Col xs={6} style={{paddingTop: "8px"}}>
                        <div className="customCard">
                            <div style={{overflow: "auto", height: "568px"}}>
                                <Timeline
                                    lineColor="steelblue"
                                >
                                   {this.timelineGenerator()}
                                </Timeline>
                            </div>
                        </div>
                    </Col>
                    <Col xs={6} style={{paddingTop: "8px"}}>
                        <Article articleNr={0} page={Pages.Merkmal} style={{fontSize: "14px"}}>
                            <h4>WLAN-Netzwerke</h4>
                                <p>Grundstzlich kann in drei verschiedene Konfigurationsmglichkeiten unterschieden werden.</p>
                            <ul>
                                <li>Independent (Ad-hoc-)WLAN</li>
                                <li>Infrastructure WLAN</li>
                                <li>Wireless Distribution System</li>
                            </ul>
                            <p>
                                <i>Independent WLAN</i> arbeiten im Peer-to-Peer-Betrieb. Jeder Rechner ist mit einer WLAN-Karte ist in diesem Netzwerk gleichberechtigt und hat die Mglichkeit, Daten zu den jeweils anderen Rechnern zu senden. Ein einzelner Rechner kann WLAN-Verbindungen zu den Partnern herstellen, die in Funkreichweite sind.
                            </p>
                            <p>
                                <b>Vorteil</b>: Wenig bis keine Konfiguration bentigt.
                            </p>
                            <p>
                                <b>Nachteil</b>: Kein Zugriff auf freigegebene Ressourcen, die nicht auf WLAN-Rechnern liegen.
                            </p>
                            <p>
                                <i>Infrastructure WLAN</i> verfgen ber eine weitere Komponente. Zustzlich zu den WLAN-Karten in den Rechnern, gibt es einen Access Point (AP). Diese erweitern die Erreichbarkeit des WLAN-Netzwerkes. Verbindungen der WLAN-Clients lassen sich nun ber den Access Point ermglichen. Der Access Point selbst besitzt zustzlich die Mglichkeit sich mit dem Netzwerk per kabelgebundenes Ethernet-Netzwerkinterface mit dem LAN zu verbinden. Ein Access Point realisiert eine Layer2-Verbindung zwischen den Protokollen IEEE 802.3 (Ethernet) und IEEE802.11 (WLAN).
                            </p>
                            <p>
                                <b>Vorteil</b>: ber den Access Point sind die WLAN-Clients somit in der Lage, auf freigegebene Ressourcen im gesamten Netzwerk zuzugreifen. Erweiterbarkeit des Bereiches in dem das WLAN erreichbar ist.
                            </p>
                            <p>
                                <b>Nachteil</b>: Konfiguration der Frequenzkanle der einzelnen Access-Points bentigt, sodass es nicht zur Kollision kommt.
                            </p>
                        </Article>
                    </Col>
                </Row>
                <Row>
                    <Col xs={6}>
                        Content ICON
                    </Col>
                    <Col xs={6}>
                        <Article articleNr={1} page={Pages.Merkmal} style={{fontSize: "14px", marginTop: "8px"}}>
                            <h4>Genutzte Frequenzen und Modulationsverfahren</h4>
                            <h5>Frequenzen</h5>
                            <dl>
                                <dt>2,412  2,484 GHz</dt>
                                    <dd>&nbsp;&nbsp;<FontAwesomeIcon icon={faChevronRight}/>Aufgeteilt in 13 Frequenzkanle mit jeweils 5MHz</dd>
                                    <dd>&nbsp;&nbsp;<FontAwesomeIcon icon={faChevronRight}/>Kanalbandbreite 20MHz (4 zusammengefasste Kanle)</dd>
                                    <dd>&nbsp;&nbsp;<FontAwesomeIcon icon={faChevronRight}/>Kanal 1, 6, 11 vollstndig berlappungsfrei</dd>
                                <dt>4,920  5 GHz</dt>
                                    <dd>&nbsp;&nbsp;<FontAwesomeIcon icon={faChevronRight}/>Nur in Japan zulssig</dd>
                                <dt>5  5,1 GHz</dt>
                                    <dd>&nbsp;&nbsp;<FontAwesomeIcon icon={faChevronRight}/>Anwendung im ffentlichen Sicherheitssektor</dd>
                                    <dd>&nbsp;&nbsp;<FontAwesomeIcon icon={faChevronRight}/>Kanalbreite 5 MHz</dd>
                                <dt>UNII-1 (5,150  5,250 GHz)</dt>
                                    <dd>&nbsp;&nbsp;<FontAwesomeIcon icon={faChevronRight}/>Indoor Verwendung</dd>
                                    <dd>&nbsp;&nbsp;<FontAwesomeIcon icon={faChevronRight}/>Nur jeder zweite Kanal (10 MHz) wird verwendet, da sonst 20 MHz nicht berlappungsfrei wren</dd>
                                <dt>UNII-2 (5,250 - 5,350 GHz)</dt>
                                    <dd>&nbsp;&nbsp;<FontAwesomeIcon icon={faChevronRight}/>Indoor verwendung</dd>
                                <dt>UNII-2 Extended (5,725 - 5,850 HGz)</dt>
                                    <dd>&nbsp;&nbsp;<FontAwesomeIcon icon={faChevronRight}/>Teilweise Radarbereich</dd>
                                <dt>Upper-ISM (5,725 - 5,850)</dt>
                                    <dd>&nbsp;&nbsp;<FontAwesomeIcon icon={faChevronRight}/>In den USA, Australien und einigen weiteren Lndern erlaubt</dd>
                                <dt>57 - 66 GHz</dt>
                                    <dd>&nbsp;&nbsp;<FontAwesomeIcon icon={faChevronRight}/>Kanalbreite 2 GHz</dd>
                            </dl>
                            <h5>Wellenlnge eines Signals</h5>
                            <p>Die Wellenlnge <i></i> und die Frequenz <i>F</i> hngen unmittelbar zusammen.
                            <i></i> ergibt sich aus der Ausbreitungsgeschwindigkeit durch <i>F</i>. Sie liegt in Abhngigkeit zu der relativen Dielektrizittskonstante. <i></i> ist in einem Kabel krzer als in der Luft oder im Vakuum (<Definition defi="Lichtgeschwindigkeit" >c</Definition>).</p>

                            <p>Je hher <i>F</i>, desto krzer <i></i></p>
                            <p>Je lnger <i>F</i>, desto niedriger <i></i></p>

                            <p>Kurze Wellenlngen haben grere Schwierigkeiten in Gebude einzudringen und geringere Reichweiten als lange Wellenlngen, sind aber wiederum schneller in ihrer bertragungsgeschwindigkeit.</p>

                        </Article>
                    </Col>
                </Row>
            </Grid>
        );
    }
}


